const SALT = '1234567890poiuytrewqasdfghjklmnbvcxzQWERTYUIOPASDFGHJKLZXCVBNM';

function randomString( length = 6 ) {
	let str = '';
	while ( 0 < length-- ) {
		const idx = Math.floor( Math.random() * SALT.length );
		str += SALT.charAt( idx );
	}
	return str;
}

export function generateId( prefix = '' ) {
	return [
		prefix,
		randomString(8),
		randomString(8),
		randomString(8),
	].join('-');
}
