import {
	CssBaseline,
	Stack,
} from '@mui/material';

import { ThemeProvider } from './theme/theme-provider';

import { Title } from './layout/title';
import { Summary } from './layout/summary/section';
import { TeamCalendar } from './layout/team-calendar/section';
import { SprintConfiguration } from './layout/sprint-configuration/section';
import { TeamManagement } from './layout/team-management/section';

export function Content() {
	return (
		<ThemeProvider>
			<CssBaseline />
			<Layout>
				<Title />
				<Summary />
				<TeamCalendar />
				<SprintConfiguration />
				<TeamManagement />
			</Layout>
		</ThemeProvider>
	);
}

function Layout( { children } ) {
	return (
		<Stack gap={ 2 }>
			{ children }
		</Stack>
	);
}
