import { TeamMembersContext } from './contexts/team-members';
import { TeamCalendarContext } from './contexts/team-calendar';
import { SprintConfigurationContext } from './contexts/sprint-configuration';

export function DataContextWrapper( { children } ) {
	return (
		<SprintConfigurationContext>
			<TeamMembersContext>
				<TeamCalendarContext>
					{ children }
				</TeamCalendarContext>
			</TeamMembersContext>
		</SprintConfigurationContext>
	);
}
