const darkPalette = {
	mode: 'dark',
	calendar: {
		background: '#202020',
	},
};

const lightPalette = {
	mode: 'light',
	primary: {
		main: '#202020',
		light: '#f0f0f0',
		dark: '#202020',
		contrastText: '#f0f0f0',
	},
	calendar: {
		background: '#ffffff',
	},
};

export function ThemeProvider( { children } ) {
	const prefersDark = useMediaQuery("(prefers-color-scheme: dark)");

	const theme = createTheme( {
		palette: prefersDark ? darkPalette : lightPalette,
		components: {
			MuiToggleButton: {
				variants: [
					{
						props: { variant: 'dark' },
						style: ( { theme } ) => ( {
							backgroundColor: alpha( theme.palette.primary.main, .05 ),

							'&:hover': {
								backgroundColor: alpha( theme.palette.primary.main, .01 ),
							},

							'&.Mui-selected': {
								backgroundColor: theme.palette.primary.main,
								color: theme.palette.primary.contrastText,
							},

							'&.Mui-selected:hover': {
								backgroundColor: alpha( theme.palette.primary.main, .9 ),
								color: theme.palette.primary.contrastText,
							},

							'&.Mui-disabled': {
								backgroundColor: alpha( theme.palette.primary.main, .05 ),
								color: theme.palette.text.disabled,
							},
						} ),
					},
				],
			},
		},
	} );

	return (
		<MuiThemeProvider theme={ theme }>
			{ children }
		</MuiThemeProvider>
	);
}
