import { createContext, useContext, useCallback } from 'react';
import { useLocalStorage } from '../hooks/use-local-storage';
import { formatDate } from '../utils/date';

const SprintConfigurationCtx = createContext( undefined );

export function useSprintConfiguration() {
	const ctx = useContext( SprintConfigurationCtx );
	if ( ! ctx ) {
		throw new Error('useTeamCalendar hook must be used within TeamCalendarContext');
	}
	return ctx;
}

export function SprintConfigurationContext( { children } ) {
	const [ sprintConfig, setSprintConfig ] = useLocalStorage( 'scp-sprint', {
		startDate: null,
		duration: 14,
		units: 'man-days',
		perDayMultiplier: 1,
		buffer: 0,
	} );

	const setStartDate = useCallback( ( date ) => {
		setSprintConfig( ( config ) => {
			return {
				...config,
				startDate: date,
			};
		} );
	}, [ setSprintConfig ] );

	const setUnits = useCallback( ( units ) => {
		setSprintConfig( ( config ) => {
			return {
				...config,
				units: units,
			};
		} );
	}, [ setSprintConfig ] );

	const setDuration = useCallback( ( duration ) => {
		setSprintConfig( ( config ) => {
			return {
				...config,
				duration: duration,
			};
		} );
	}, [ setSprintConfig ] );

	const setPerDayMultiplier = useCallback( ( spPerDay ) => {
		setSprintConfig( ( config ) => {
			return {
				...config,
				storyPointsPerDay: spPerDay,
			};
		} );
	}, [ setSprintConfig ] );

	const setBuffer = useCallback( ( buffer ) => {
		setSprintConfig( ( config ) => {
			return {
				...config,
				buffer: buffer,
			};
		} );
	}, [ setSprintConfig ] );

	const calendar = () => {
		const res = [];
		const dateIterator = new Date( sprintConfig.startDate );
		for ( let i = 0, dur = sprintConfig.duration; i < dur; i++ ) {
			res.push( formatDate( dateIterator ) );
			dateIterator.setDate( dateIterator.getDate() + 1 );
		}
		return res;
	};

	const context = {
		sprintConfig,

		calendar,

		setStartDate,
		setUnits,
		setPerDayMultiplier,
		setDuration,
		setBuffer,
	};

	return (
		<SprintConfigurationCtx.Provider value={ context }>
			{ children }
		</SprintConfigurationCtx.Provider>
	);
}
