import { useState } from 'react';
import {
	Paper,
	Stack,
	Box,
	Divider,

	Avatar,

	TextField,
	ToggleButtonGroup,
	ToggleButton as MuiToggleButton,
	Button,
	IconButton,
} from '@mui/material';

import {
	Groups,
	Delete,
	PeopleOutline,
	PersonAddAlt,
	PersonOutlineOutlined as ActiveIcon,
	PersonOffOutlined as InactiveIcon,
 } from '@mui/icons-material';

import { SectionTitle } from '../section-title';

import { useTeamMembers } from '../../contexts/team-members';
import { muiStringAvatar } from '../../utils/avatar';

export function TeamManagement() {
	console.log( 'TeamManagement' );

	return (
		<Paper>
			<SectionTitle icon={ PeopleOutline }>
				<h1>Team</h1>
			</SectionTitle>
			<Divider />
			<MembersList />
			<Divider />
			<NewMember />
		</Paper>
	);
}


function NewMember() {
	const { addTeamMember } = useTeamMembers();

	const [ name, setName ] = useState( '' );
	const [ workSchedule, setWorkSchedule ] = useState( 'sun-thu' );

	const handleAdd = () => {
		addTeamMember( name, workSchedule );
		setName('');
	};

	const isNameEmpty = ! name.length;

	return (
		<Stack flexDirection="row" gap={ 1.5 } alignItems="center" justifyContent="space-between" sx={ { p: 2 } }>
			<PersonAddAlt />

			<Box flexGrow={ 1 }>
				<TextField
					fullWidth
					value={ name }
					placeholder='Name or Nickname'
					size="small"
					onChange={ ( e ) => setName( e.target.value ) }
				/>
			</Box>

			<ToggleButtonGroup
				exclusive
				size="small"
				color="warning"
				value={ workSchedule }
				disabled={ isNameEmpty }
				onChange={ ( _, v ) => setWorkSchedule( v ) }
			>
				<ToggleButton value="mon-fri">Mon-Fri</ToggleButton>
				<ToggleButton value="sun-thu">Sun-Thu</ToggleButton>
			</ToggleButtonGroup>

			<Button variant="contained" disabled={ isNameEmpty } onClick={ () => handleAdd() }>Add Member</Button>
		</Stack>
	);
}

function MembersList() {
	console.log('MembersList');

	const { teamMembers, removeTeamMember, updateWorkSchedule, toggleStatus } = useTeamMembers();

	if ( ! teamMembers.length ) {
		return (
			<Stack alignItems="center" sx={ { p: 12 } }>
				<Groups sx={ { fontSize: '8rem', opacity: .20 } } />
				<p>Right now the list of team members is empty.</p>
				<p>Start by adding your first team member!</p>
			</Stack>
		);
	}

	return (
		<Stack gap={ 1.25 } sx={ { p: 2 } }>
			{ teamMembers.map( ( member ) => {
				const memberId = member.id;
				return (
					<MemberCard
						key={ memberId }
						member={ member }
						onToggleStatus={ () => toggleStatus( memberId ) }
						onChangeSchedule={ ( newSchedule ) => updateWorkSchedule( memberId, newSchedule ) }
						onRemove={ () => removeTeamMember( memberId ) }
					/>
				);
			} ) }
		</Stack>
	);
}

function MemberCard( { member, onRemove, onChangeSchedule, onToggleStatus } ) {
	return (
		<Paper variant="outlined" sx={ { p: 1 } }>
			<Stack flexDirection="row" gap={ 1.25 } alignItems="center" justifyContent="space-between">
				<Avatar { ...muiStringAvatar( member ) } />

				<Box flexGrow={ 1 }>{ member.name }</Box>

				<IconButton onClick={ () => onToggleStatus() }>
					<MemberStatusIcon status={ member.status } />
				</IconButton>

				<ToggleButtonGroup
					exclusive
					color="warning"
					size="small"
					value={ member.schedule }
					onChange={ ( _, newSchedule ) => newSchedule && onChangeSchedule( newSchedule ) }
				>
					<ToggleButton value="mon-fri">Mon-Fri</ToggleButton>
					<ToggleButton value="sun-thu">Sun-Thu</ToggleButton>
				</ToggleButtonGroup>

				<IconButton disabled={ ! onRemove } onClick={ () => onRemove() } sx={ {
					color: 'silver',
					"&:hover": {
						color: 'red',
					},
				} }>
					<Delete />
				</IconButton>
			</Stack>
		</Paper>
	);
}

function MemberStatusIcon( { status } ) {
	if ( 'inactive' === status ) {
		return <InactiveIcon sx={ { color: 'silver' } } />
	}
	return <ActiveIcon sx={ { color: 'green' } } />
}

function ToggleButton( { children, ...props } ) {
	return (
		<MuiToggleButton
			variant="dark"
			sx={ { textTransform: 'lowercase', px: 2 } }
			{ ...props }
		>{ children }</MuiToggleButton>
	);
}
