import { Paper, Box, Stack, Typography } from '@mui/material';
import { CalendarMonthSharp as CalendarIcon } from '@mui/icons-material';

import { useSprintConfiguration } from '../../contexts/sprint-configuration';
import { useTeamCalendar } from '../../contexts/team-calendar';
import { useTeamMembers } from '../../contexts/team-members';

import { formatDate } from '../../utils/date';

function sprintDuration( duration ) {
	if ( 28 === duration ) {
		return [ 4, 'weeks' ];
	}

	if ( 21 === duration ) {
		return [ 3, 'weeks' ];
	}

	if ( 14 === duration ) {
		return [ 2, 'weeks' ];
	}

	if ( 7 === duration ) {
		return [ 1, 'week' ];
	}

	return [ duration, 'day(s)' ];
}

const workWeek = {
	'mon-fri': [ 1, 2, 3, 4, 5 ],
	'sun-thu': [ 0, 1, 2, 3, 4 ],
};

function labelUnits( units ) {
	if ( 'story-points' === units ) {
		return 'Story-Points';
	}

	return 'Man-Days';
}

function calculateTotal( { sprintConfig, teamMembers, daysOff, holidays } ) {
	let total = 0;
	let teamTotal = 0;
	let teamActive = 0;

	let numberOfHolidays = 0;
	let numberOfDaysOff = 0;

	const perDayMultiplier = ( 'story-points' === sprintConfig.units )
		? sprintConfig.storyPointsPerDay
		: 1;

	const buffer = sprintConfig.buffer;

	const sprintDays = [];
	const dateIterator = new Date( sprintConfig.startDate ?? null );

	for ( let i = 0, dur = sprintConfig.duration; i < dur; i++ ) {
		sprintDays.push( formatDate( dateIterator ) );
		dateIterator.setDate( dateIterator.getDate() + 1 );
	}

	sprintDays.forEach( ( date ) => {
		if ( holidays.some( ( item ) => item.date === date ) ) {
			numberOfHolidays++;
		}
	} );

	teamMembers.forEach( ( member ) => {
		teamTotal++;

		if ( 'inactive' === member.status ) {
			return;
		}

		teamActive++;

		sprintDays.forEach( ( date ) => {
			const theDate = new Date( date );
			const theDateString = formatDate( theDate );

			const workSchedule = workWeek[ member.schedule ];

			if ( -1 === workSchedule.indexOf( theDate.getDay() ) ) {
				return;
			}

			if ( holidays.some( ( item ) => item.date === theDateString ) ) {
				return;
			}

			if ( daysOff.some( ( item ) => item.memberId === member.id && item.date === theDateString ) ) {
				numberOfDaysOff++;
				return;
			}

			total += 1 * perDayMultiplier;
		} );
	} );

	const bufferAdjustment = Math.ceil( total * .01 * buffer );

	return {
		rawTotal: total,
		totalCapacity: total - bufferAdjustment,

		buffer: buffer,
		bufferAdjustment,

		numberOfHolidays,
		numberOfDaysOff,

		teamFull: teamTotal,
		teamAvailable: teamActive,
	};
}

export function Summary() {
	console.log( 'Summary' );

	const { sprintConfig } = useSprintConfiguration();
	const { teamMembers } = useTeamMembers();
	const { holidays, daysOff } = useTeamCalendar();

	const [ duration, durationUnits ] = sprintDuration( sprintConfig.duration );

	const {
		rawTotal,
		totalCapacity,

		numberOfHolidays,
		numberOfDaysOff,

		buffer,
		bufferAdjustment: spAdjustment,

		teamFull,
		teamAvailable
	} = calculateTotal( {
		sprintConfig,
		teamMembers,
		holidays,
		daysOff,
	} );

	return (
		<Paper className="summary" sx={ { py: 2, bgcolor: '#202020', color: 'white', fontFamily: 'monospace' } }>
			<Stack flexDirection="row" alignItems="center" justifyContent="space-between" >
				<Box>
					<Capacity
						value={ totalCapacity }
						units={ labelUnits( sprintConfig.units ) }
					/>
					<FullCapacity
						total={ rawTotal }
						buffer={ buffer }
						adjustment={ spAdjustment }
						adjustmentUnits={ labelUnits( sprintConfig.units ) }
					/>
				</Box>

				<Stack gap={ .5 }>
					<Box>
						<Typography>Team: { teamAvailable } / { teamFull }</Typography>
					</Box>
					<Box>
						<Typography>Holidays: { numberOfHolidays } / Days Off: { numberOfDaysOff }</Typography></Box>
					<Box>
						<Typography>Duration: { duration } { durationUnits }</Typography>
					</Box>
					<Box>
						<Typography>Buffer: { buffer }%</Typography>
					</Box>
				</Stack>
			</Stack>
		</Paper>
	);
}

function Capacity( { value: totalCapacity, units } ) {
	return (
		<>
			<Stack flexDirection={ 'row' } gap={ 1 } alignItems={ 'center' }>
				<CalendarIcon fontSize="small" />
				<p>Sprint Capacity:</p>
			</Stack>
			<Box>
				<h1>
					<strong>{ totalCapacity }</strong> <sub>{ units }</sub>
				</h1>
			</Box>
		</>
	);
}

function FullCapacity( props ) {
	const { total: rawTotal, buffer, adjustment: spAdjustment } = props;

	if ( 0 === rawTotal || 0 === buffer ) {
		return null;
	}

	return (
		<p>Full: { rawTotal } / Buffer: { buffer }% (-{ spAdjustment })</p>
	);
}
