import { useState, useEffect } from 'react';
import { Settings as SettingsIcon } from '@mui/icons-material';
import {
	Typography,
	Stack,
	Paper,
	Divider,

	Slider as MuiSlider,
	ToggleButtonGroup,
	ToggleButton as MuiToggleButton,
} from '@mui/material';

import { DatePicker } from '@mui/x-date-pickers';
import dayjs from 'dayjs';

import { SectionTitle } from '../section-title';
import { useSprintConfiguration } from '../../contexts/sprint-configuration';

export function SprintConfiguration() {
	console.log( 'Sprint Configuration' );

	return (
		<Paper component="section">
			<SectionTitle>
				<SettingsIcon className="section-icon" />
				<h1>Sprint Configuration</h1>
			</SectionTitle>

			<Divider />

			<Stack gap={ 2 } sx={ { p: 2, px: 4 } }>
				<Stack flexDirection="row" alignItems={ 'center' } gap={ 1 }>
					<Stack>
						<StartDate />
					</Stack>
					<Stack flexGrow={ 1 }>
						<Duration />
					</Stack>
				</Stack>

				<Units />

				<Stack>
					<Buffer />
				</Stack>
			</Stack>
		</Paper>
	);
}

function StartDate() {
	console.log( 'Start Date' );

	const {
		sprintConfig,
		setStartDate,
	} = useSprintConfiguration();

	const [ selectedDate, setSelectedDate ] = useState( () => {
		if ( sprintConfig.startDate ) {
			return dayjs( sprintConfig.startDate, 'YYYY-MM-DD', true );
		}
		return dayjs();
	} );

	useEffect( () => {
		setStartDate( selectedDate.format('YYYY-MM-DD') );
	}, [ selectedDate, setStartDate ] );

	return (
		<>
			<Stack>
				<FieldLabel>Start date:</FieldLabel>
				<DatePicker
					fullWidth
					value={ selectedDate }
					onChange={ setSelectedDate }
					slotProps={ {
						textField: {
							size: 'small',
						},
					} }
				/>
			</Stack>
		</>
	);
}

function Duration() {
	console.log( 'Duration' );

	const {
		sprintConfig,
		setDuration,
	} = useSprintConfiguration();

	const [ selectedDuration, setSelectedDuration ] = useState( () => {
		if ( sprintConfig.duration ) {
			return sprintConfig.duration;
		}
		return 14;
	} );

	useEffect( () => {
		setDuration( selectedDuration );
	}, [ selectedDuration, setDuration ] );

	return (
		<>
			<Stack>
				<FieldLabel>Sprint Duration:</FieldLabel>
				<ToggleButtonGroup
					value={ selectedDuration }
					onChange={ ( _, newDuration ) => newDuration && setSelectedDuration( newDuration ) }
					exclusive
					color="primary"
					size="small"
					fullWidth
				>
					<ToggleButton value={ 7 }>1 week</ToggleButton>
					<ToggleButton value={ 14 }>2 weeks</ToggleButton>
					<ToggleButton value={ 21 }>3 weeks</ToggleButton>
					<ToggleButton value={ 28 }>4 weeks</ToggleButton>
				</ToggleButtonGroup>
			</Stack>
		</>
	);
}

function Units() {
	console.log( 'Units' );

	const {
		sprintConfig,
		setUnits,
		setPerDayMultiplier,
	} = useSprintConfiguration();

	const [ selectedUnits, setSelectedUnits ] = useState( () => sprintConfig.units );
	const [ storyPointsPerDay, setStoryPointsPerDay ] = useState( () => sprintConfig.storyPointsPerDay );

	useEffect( () => {
		setUnits( selectedUnits );
	}, [ selectedUnits, setUnits ] );

	useEffect( () => {
		setPerDayMultiplier( storyPointsPerDay );
	}, [ storyPointsPerDay, setPerDayMultiplier ] );

	const spEnabled = 'story-points' === sprintConfig.units;

	return (
		<>
			<Stack>
				<FieldLabel>Units:</FieldLabel>
				<ToggleButtonGroup
					value={ selectedUnits }
					onChange={ ( _, value ) => value && setSelectedUnits( value ) }
					exclusive
					color="primary"
					size="small"
					fullWidth
				>
					<ToggleButton value="man-days">Man Days</ToggleButton>
					<ToggleButton value="story-points">Story Points</ToggleButton>
				</ToggleButtonGroup>
			</Stack>

			{ spEnabled && (
				<Stack>
					<FieldLabel>Story-Points per Day:</FieldLabel>
					<ToggleButtonGroup
						value={ storyPointsPerDay }
						onChange={ ( _, value ) => value && setStoryPointsPerDay( value ) }
						exclusive
						variant="dark"
						color="primary"
						size="small"
						fullWidth
					>
						<ToggleButton value={ 1 }>1 SP</ToggleButton>
						<ToggleButton value={ 2 }>2 SP</ToggleButton>
						<ToggleButton value={ 4 }>4 SP</ToggleButton>
						<ToggleButton value={ 8 }>8 SP</ToggleButton>
					</ToggleButtonGroup>
				</Stack>
			) }
		</>
	);
}

function Buffer() {
	console.log( 'Buffer' );

	const {
		sprintConfig,
		setBuffer,
	} = useSprintConfiguration();

	const [ slackBuffer, setSlackBuffer ] = useState( () => sprintConfig.buffer );

	useEffect( () => {
		setBuffer( slackBuffer )
	}, [ slackBuffer, setBuffer ] );

	const bufferValue = ( 0 < slackBuffer )
		? ` ${ slackBuffer }%`
		: '';

	return (
		<>
			<FieldLabel>Buffer{ bufferValue }:</FieldLabel>
			<Slider
				size="medium"
				value={ slackBuffer }
				onChange={ ( _, value ) => setSlackBuffer( value ) }
				step={ 5 }
				max={ 75 }
				marks={ [
					{ value: 0, label: '0%' },
					{ value: 15, label: '15%' },
					{ value: 30, label: '30%' },
					{ value: 45, label: '45%' },
					{ value: 60, label: '60%' },
					{ value: 75, label: '75%' },
				] }
				valueLabelDisplay="auto"
			/>
		</>
	);
}

function ToggleButton( { children, ...props } ) {
	return (
		<MuiToggleButton variant="dark" { ...props }>{ children }</MuiToggleButton>
	);
}

function Slider( { ...props } ) {
	return (
		<MuiSlider { ...props } sx={ {
			height: 4,

			'& .MuiSlider-thumb': {
				width: 14,
				height: 14,
			},

			'& .MuiSlider-track': {
				height: 4,
			},

			'& .MuiSlider-rail': {
				height: 4,
				opacity: 0.5,
			},

			'& .MuiSlider-markLabel': {
				fontSize: '0.7rem',
				opacity: 0.7,
			},
		} } />
	);
}

function FieldLabel( { children } ) {
	return (
		<Typography className="field-label" component="p">{ children }</Typography>
	);
}
