function stringToColor(string) {
	let hash = 0;
	let i;

	/* eslint-disable no-bitwise */
	for (i = 0; i < string.length; i += 1) {
		hash = string.charCodeAt(i) + ((hash << 5) - hash);
	}

	let color = '#';

	for (i = 0; i < 3; i += 1) {
		const value = (hash >> (i * 8)) & 0xff;
		color += `00${value.toString(16)}`.slice(-2);
	}
	/* eslint-enable no-bitwise */

	return color;
}

export function muiStringAvatar( member ) {
	const [ firstName, lastName = '' ] = member.name.split(' ');

	const initials = [
		firstName ? firstName[0] : '',
		lastName ? lastName[0] : '',
	].join('').toUpperCase();

	return {
		sx: {
			bgcolor: stringToColor(`${member.name}${member.id}`),
			color: '#fafafa',
			width: 32,
			height: 32,
			fontSize: 12,
			fontWeight: 500,
			textShadow: 'black 1px 1px 4px',
			boxShadow: 'rgba(40, 40, 40, .5) 1px 1px 2px',
		},
		children: initials,
	};
}
